/* CreateBucket.css */

/* Contenedor general que centra el contenido horizontalmente y lo alinea en la parte superior */
.create-bucket-container {
    display: flex;
    justify-content: center;
    align-items: flex-start; /* Alinea en la parte superior */
    min-height: 100vh;
    background-color: #1f2937; /* Gris oscuro, similar a bg-gray-800 */
    margin: 0;
    padding: 2rem 0; /* Espacio superior e inferior */
}

/* Contenedor del recuadro (la caja del bucket) */
.create-bucket-box {
    background-color: #000; /* Fondo negro */
    border: 1px solid #374151; /* Borde gris (equivalente a border-gray-700) */
    border-radius: 0.5rem; /* rounded-lg */
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1); /* Sombra similar a shadow-lg */
    padding: 1.5rem; /* p-6 */
    max-width: 32rem; /* max-w-lg */
    width: 100%;
    margin: 1rem;
}

/* Título principal */
.create-bucket-box h2 {
    color: #fff; /* Títulos en blanco */
    text-align: center;
    font-size: 1.875rem; /* Aproximadamente 3xl */
    font-weight: bold;
    margin-bottom: 1.5rem;
}
